<template>
  <div class="about">
    <h1>This is an about page</h1>
    <button @click="addItem">더하기</button>
    <button @click="addBulk(4)">많이 더하기</button>
    <ul id="list">
      <li v-for="item in items" :key="item.id">{{ item.body }}</li>
    </ul>
  </div>
</template>

<script>
import { v4 as uuid } from "uuid";
//import signalR from "@microsoft/signalr";
//import $ from "jquery";

export default {
  data: function() {
    return {
      items: [
        { id: uuid(), body: "hello vue" },
        { id: uuid(), body: "hello vue 2" }
      ]
    };
  },
  mounted() {},
  methods: {
    addItem() {
      const id = uuid();
      this.items.push({ id: id, body: `hello vue ${id}` });

      this.scrollToBottom();
    },
    scrollToBottom() {
      setTimeout(() => {
        const element = document.getElementById("list");
        element.scrollTop = element.scrollHeight - element.clientHeight;
      }, 1);

      // var list = $("#list");
      // list.animate({ scrollTop: list.prop("scrollHeight") }, 250);
    },
    addBulk(count) {
      const newItems = [];

      for (let i = 0; i < count; i++) {
        const id = uuid();
        newItems.push({ id: id, body: `hello vue ${id}` });
      }

      this.items = this.items.concat(newItems);
      this.scrollToBottom();
    }
  }
};
</script>

<style scoped>
#list {
  overflow: scroll;
  height: 200px;
  background: #eee;
}
</style>
